// Admin.jsx
import React, { useState, useEffect } from 'react';
import { useAuth } from '../../contexts/AuthContext';
import { Alert, Button, Card, Col, Container, Form, Image, Spinner } from 'react-bootstrap';
import { useImages } from '../../contexts/ImageContext';
import './Admin.css';
import ImageDetailModal from '../../components/ImageDetailModal';

const Admin = () => {
    const { user, logout } = useAuth();
    const { imageList, addImage, deleteImage, loading } = useImages();

    const [image, setImage] = useState(null);
    const [description, setDescription] = useState('');
    const [error, setError] = useState(null);
    const [success, setSuccess] = useState(null);

    const [showModal, setShowModal] = useState(null);
    const [selectedImage, setSelectedImage] = useState(null);
    const handShowDetail = (image) => {
        setSelectedImage(image);
        setShowModal(true);
    }

    const handleUpload = async () => {
        if (!image) {
            setError('Vui lòng chọn ảnh');
            return;
        }

        const formData = new FormData();
        formData.append('image', image);
        formData.append('description', description);

        try {
            await addImage(formData);
            setSuccess('Tải ảnh lên thành công!');
            setImage(null);
            setDescription('');
            setError(null);
        } catch (err) {
            setError(err.response?.data?.message || 'Upload failed');
        }
    };

    const handleDelete = async (id) => {
        if (!window.confirm('Bạn chắc chắn muốn xóa ảnh này?')) return;
        try {
            await deleteImage(id);
            setSuccess('Xóa ảnh thành công!');
            setError(null);
        } catch (err) {
            setError(err.response?.data?.message || 'Delete failed');
        }
    };

    useEffect(() => {
        const timer = setTimeout(() => {
            setError(null);
            setSuccess(null);
        }, 3000);
        return () => clearTimeout(timer);
    }, [error, success]);

    return (
        <Container className="py-5 adminPage">
            <Card className="mb-4 shadow-sm border-0 bg-light bg-opacity-75">
                <Card.Body className="d-flex justify-content-between align-items-center">
                    <div>
                        <h5 className="mb-0 fw-semibold text-dark">👋 Chào, {user?.username}</h5>
                        <small className="text-muted">Chào mừng bạn quay lại trang quản trị</small>
                    </div>
                    <Button variant="" onClick={logout} >
                        <img src="/assets/shared/img/logout.svg" alt="logout" />
                    </Button>
                </Card.Body>
            </Card>

            {error && (
                <Alert variant="danger" dismissible onClose={() => setError(null)} className="rounded shadow-sm">
                    {error}
                </Alert>
            )}
            {success && (
                <Alert variant="success" dismissible onClose={() => setSuccess(null)} className="rounded shadow-sm">
                    {success}
                </Alert>
            )}

            <Card className="mb-5 shadow-sm border-0 bg-white bg-opacity-75">
                <Card.Body>
                    <Card.Title className="mb-3">📤 Tải ảnh mới</Card.Title>
                    <Form>
                        <Form.Group className="mb-3">
                            <Form.Control
                                type="file"
                                accept="image/*"
                                onChange={(e) => setImage(e.target.files[0])}
                                disabled={loading}
                                className="rounded-pill px-3 py-2"
                            />
                        </Form.Group>

                        {image && (
                            <div className="mb-3 text-center">
                                <Image
                                    src={URL.createObjectURL(image)}
                                    thumbnail
                                    className="shadow-sm"
                                    style={{ maxHeight: '200px' }}
                                />
                            </div>
                        )}

                        <Form.Group className="mb-3">
                            <Form.Control
                                as="textarea"
                                rows={3}
                                value={description}
                                onChange={(e) => setDescription(e.target.value)}
                                disabled={loading}
                                placeholder="📝 Viết mô tả cho ảnh..."
                                className="rounded-3"
                            />
                        </Form.Group>

                        <Button
                            variant="success"
                            onClick={handleUpload}
                            disabled={loading}
                            className="rounded-pill px-4"
                        >
                            {loading ? (
                                <>
                                    <Spinner as="span" size="sm" animation="border" role="status" />
                                    <span className="ms-2">Đang tải lên...</span>
                                </>
                            ) : 'Tải ảnh lên'}
                        </Button>
                    </Form>
                </Card.Body>
            </Card>

            <h5 className="text-dark mb-3">📒Danh sách ảnh đã tải lên</h5>

            {loading && imageList.length === 0 ? (
                <div className="text-center py-4">
                    <Spinner animation="border" role="status" />
                </div>
            ) : imageList.length === 0 ? (
                <p className="text-muted text-center py-4">Bạn chưa tải ảnh nào lên.</p>
            ) : (
                <>
                    {imageList.map((image) => (

                        <Col key={image.id} xs={12} onClick={() => handShowDetail(image)}>
                            <Card className="shadow-sm flex-row align-items-center mb-2" >
                                <Card.Img
                                    src={`${process.env.REACT_APP_API_URL}${image.url}`}
                                    style={{
                                        width: '100px',
                                        height: '100px',
                                        objectFit: 'cover',
                                        borderTopLeftRadius: '0.375rem',
                                        borderBottomLeftRadius: '0.375rem'
                                    }}
                                />
                                <Card.Body className="d-flex flex-column justify-content-between" >
                                    <div className="mb-2">
                                        <Card.Text className="mb-1 fw-semibold text-dark" >
                                            {image.description
                                                ? image.description.length > 50
                                                    ? `${image.description.slice(0, 50)}...`
                                                    : image.description
                                                : <em className="fw-normal text-muted">(Không có mô tả)</em>
                                            }
                                        </Card.Text>

                                    </div>
                                    <div className="d-flex justify-content-between align-items-center">
                                        <small className="text-muted">{new Date(image.id).toLocaleString()}</small>
                                        <Button
                                            variant=""
                                            size="sm"
                                            onClick={(e) => {
                                                e.stopPropagation(); // Ngăn lan truyền
                                                handleDelete(image.id);
                                            }}
                                            disabled={loading}
                                        >
                                            ❌
                                        </Button>

                                    </div>
                                </Card.Body>
                            </Card>
                        </Col>


                    ))}
                </>
            )}

            <ImageDetailModal
                show={showModal}
                onHide={() => setShowModal(false)}
                image={selectedImage}
            />
        </Container>
    );
};

export default Admin;
