// MediaPage.jsx
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuth } from '../../contexts/AuthContext';
import LoginModal from '../../components/LoginModal';
import ImageDetailModal from '../../components/ImageDetailModal';
import ImageGallery from '../../components/ImageGallery';
import { Container, Button, Card } from 'react-bootstrap';
import './MediaPage.css';
import { motion } from 'framer-motion';
import { useImages } from '../../contexts/ImageContext';

const MediaPage = () => {
    const { user } = useAuth();
    const navigate = useNavigate();
    const { imageList } = useImages();

    const [showLogin, setShowLogin] = useState(false);
    const [showModal, setShowModal] = useState(false);
    const [selectedImage, setSelectedImage] = useState(null);

    const handleImageClick = (image) => {
        setSelectedImage(image);
        setShowModal(true);
    };

    const handleAddImage = () => {
        if (user) {
            navigate('/admin');
        } else {
            setShowLogin(true);
        }
    };
    useEffect(() => {
        console.log('MediaPage MOUNTED');
        return () => console.log('MediaPage UNMOUNTED');
      }, []);

    return (
        <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6 }}
            className="media-page-wrapper"
        >
            <Container>
                <Card className="mb-4 shadow-sm glass-card border-0 bg-light bg-opacity-75">
                    <Card.Body className="d-flex justify-content-between align-items-center">
                        <h4 className="mb-0">📷 Thư viện ảnh</h4>
                        <Button variant="" className="text-success btn-outline" onClick={handleAddImage}>
                        <img src="/assets/shared/img/add.svg" alt="add"  />
                        </Button>
                    </Card.Body>
                </Card>
                <ImageGallery imageList={imageList} handleImageClick={handleImageClick} />
            </Container>

            <ImageDetailModal
                show={showModal}
                onHide={() => setShowModal(false)}
                image={selectedImage}
            />

            <LoginModal
                show={showLogin}
                onHide={() => setShowLogin(false)}
            />
        </motion.div>
    );
};

export default MediaPage;
