import React from 'react';
import { Modal, Image } from 'react-bootstrap';
import './ImageDetailModal.css';

const ImageDetailModal = ({ show, onHide, image }) => {
    if (!image) return null;

    const formattedDate = new Date(image.id).toLocaleDateString('vi-VN', {
        day: '2-digit',
        month: '2-digit',
        year: 'numeric',
    });

    return (
        <Modal
            show={show}
            onHide={onHide}
            size="lg"
            centered
            contentClassName="custom-modal"
        >
            <Modal.Header closeButton className="border-0">
                <Modal.Title className="w-100 text-center fs-5 ">
                    🗓️ {formattedDate}
                </Modal.Title>
            </Modal.Header>
            <Modal.Body className="text-center">
                <Image
                    src={`${process.env.REACT_APP_API_URL}${image.url}`}
                    alt="Ảnh chi tiết"
                    fluid
                    className="modal-image mb-3"
                />
                {image.description && (
                    <p className="text-50">{image.description}</p>
                )}
            </Modal.Body>
        </Modal>
    );
};

export default ImageDetailModal;
