import { useEffect } from 'react';
import './BirthdayText.css'; // File CSS riêng

const BirthdayText = () => {
  useEffect(() => {
    const birthdayText = document.getElementById('BirthdayText');
    if (birthdayText) {
      // Thêm class animation khi component mount
      birthdayText.classList.add('animate-once');
      
      // Xóa animation sau khi hoàn thành (tổng thời gian: fadeIn 4s + delay 6s + fadeOut 3s = 13s)
      const cleanupTimer = setTimeout(() => {
        birthdayText.classList.remove('animate-once');
        birthdayText.style.opacity = '0'; // Reset về trạng thái ban đầu
      }, 8000);

      return () => clearTimeout(cleanupTimer); // Cleanup khi component unmount
    }
  }, []);

  return (
    <div id="BirthdayText">
      <div className="text text1">
        {['H', 'A', 'P', 'P', 'Y', '', 'B', 'I', 'R', 'T', 'H', 'D', 'A', 'Y'].map((letter, index) => (
          <span key={`letter1-${index}`} className="letter">
            {letter}
          </span>
        ))}
      </div>
      <div className="text text2">
        {['T', 'H', 'U', '', 'H', 'U', 'Y', 'E', 'N'].map((letter, index) => (
          <span key={`letter2-${index}`} className="letter">
            {letter}
          </span>
        ))}
        <span>
          <img id="bubu-play" src="assets/shared/img/bubu-play.gif" alt="bubu" />
        </span>
      </div>
    </div>
  );
};

export default BirthdayText;