import React, { useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import { Modal, Button, Form, Alert } from 'react-bootstrap';
import './LoginModal.css';

const LoginModal = ({ show, onHide }) => {
  const { login } = useAuth();
  const navigate = useNavigate();

  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [message, setMessage] = useState('');

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const success = await login(username, password);
      if (success) {
        onHide();
        setMessage("")
        navigate('/admin');
      } else {
        setMessage('Tài khoản hoặc mật khẩu sai!');
      }
    } catch (err) {
      setMessage('Đã xảy ra lỗi');
    }
  };

  return (
    <Modal show={show} onHide={onHide} centered contentClassName="login-modal-glass">
      <Modal.Header closeButton className="border-0">
        <Modal.Title className="w-100 text-center text-white">🔐 Đăng nhập</Modal.Title>
      </Modal.Header>

      <Modal.Body>
        <Form onSubmit={handleSubmit}>
          {
            message && (<Alert
              variant='danger'
              className="text-center rounded-pill py-2"
            >
              {message}
            </Alert>)
          }

          <Form.Group className="mb-3">
            <Form.Control
              type="text"
              placeholder="👤 Tên đăng nhập"
              value={username}
              onChange={(e) => setUsername(e.target.value)}
              className="rounded-pill px-3 py-2"
              required
            />
          </Form.Group>

          <Form.Group className="mb-4">
            <Form.Control
              type="password"
              placeholder="🔒 Mật khẩu"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              className="rounded-pill px-3 py-2"
              required
            />
          </Form.Group>

          <div className="d-grid gap-2">
            <Button variant="light" type="submit" className="rounded-pill" id="custom-login-btn">
              Đăng nhập
            </Button>
          </div>
        </Form>
      </Modal.Body>
    </Modal>
  );
};

export default LoginModal;
