import React, { createContext, useContext, useState, useCallback, useEffect } from 'react';
import axios from 'axios';

const ImageContext = createContext();

export const ImageProvider = ({ children }) => {
  const [imageList, setImageList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [imageCache, setImageCache] = useState({});

  // API endpoints
  const API_BASE_URL = process.env.REACT_APP_API_URL || '';

  const fetchImages = useCallback(async () => {
    try {
      setLoading(true);
      const response = await axios.get(`${API_BASE_URL}/images`);
      setImageList(response.data.map(img => ({
        id: img.id,
        description: img.description,
        createdAt: img.created_at,
        imageType: img.image_type
      })));
    } catch (err) {
      console.error('Error fetching images:', err);
      throw err;
    } finally {
      setLoading(false);
    }
  }, [API_BASE_URL]);

  const addImage = async (file, description) => {
    try {
      const formData = new FormData();
      formData.append('image', file);
      formData.append('description', description);

      const response = await axios.post(`${API_BASE_URL}/images`, formData, {
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      });

      const newImage = {
        id: response.data.id,
        description: response.data.description,
        createdAt: response.data.created_at,
        imageType: response.data.image_type
      };

      setImageList(prev => [newImage, ...prev]);
      return newImage;
    } catch (err) {
      console.error('Error uploading image:', err);
      throw err;
    }
  };

  const deleteImage = async (id) => {
    try {
      await axios.delete(`${API_BASE_URL}/images/${id}`);
      setImageList(prev => prev.filter(img => img.id !== id));
      // Xóa khỏi cache nếu có
      setImageCache(prev => {
        const newCache = { ...prev };
        delete newCache[id];
        return newCache;
      });
    } catch (err) {
      console.error('Error deleting image:', err);
      throw err;
    }
  };

  const getImageData = async (id) => {
    try {
      // Kiểm tra cache trước
      if (imageCache[id]) {
        return imageCache[id];
      }

      const response = await axios.get(`${API_BASE_URL}/images/${id}`, {
        responseType: 'blob'
      });

      const imageUrl = URL.createObjectURL(response.data);
      
      // Lưu vào cache
      setImageCache(prev => ({
        ...prev,
        [id]: imageUrl
      }));

      return imageUrl;
    } catch (err) {
      console.error('Error getting image data:', err);
      return null;
    }
  };

  // Clear cache khi unmount
  useEffect(() => {
    return () => {
      Object.values(imageCache).forEach(url => URL.revokeObjectURL(url));
    };
  }, [imageCache]);

  useEffect(() => {
    fetchImages();
  }, [fetchImages]);

  return (
    <ImageContext.Provider value={{ 
      imageList, 
      fetchImages, 
      addImage, 
      deleteImage,
      getImageData,
      loading 
    }}>
      {children}
    </ImageContext.Provider>
  );
};

export const useImages = () => useContext(ImageContext);