import { Link, useLocation } from 'react-router-dom';
import MusicPlayer from './MusicPlayer';
import Share from './Share';
import './Navbar.css';

export default function Navbar() {
    const location = useLocation();

    return (
        <div className={`custom-navbar shadow-lg ${location.pathname === "/" ? "": "bg_black"}`}
            onClick={e=>e.stopPropagation()}
        >
            <MusicPlayer />|
            <Link
                to="/"
                className={`nav-icon-btn ${location.pathname === '/' ? 'active myActive' : ''}`}
                title="Trang chủ"
            >
                <img src="/assets/shared/img/cake.svg" alt="Home" className="nav-icon" />
            </Link>

            <Link
                to="/media"
                className={`nav-icon-btn ${location.pathname === '/media' ? 'active myActive' : ''}`}
                title="Thư viện ảnh"
            >
                <img src="/assets/shared/img/photos.svg" alt="Media" className="nav-icon" />
            </Link>
            |<Share />
        </div>
    );
}
