import React from 'react';
import { Modal, Button } from 'react-bootstrap';
import './ShareModal.css';

const ShareModal = ({ show, onHide }) => {
  const url = window.location.href;

  const handleShare = (platform) => {
    const encodedUrl = encodeURIComponent(url);
    let shareUrl = '';

    switch (platform) {
      case 'facebook':
        shareUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodedUrl}`;
        break;
      case 'twitter':
        shareUrl = `https://twitter.com/intent/tweet?url=${encodedUrl}`;
        break;
      case 'telegram':
        shareUrl = `https://t.me/share/url?url=${encodedUrl}`;
        break;
      case 'zalo':
        shareUrl = `https://zalo.me/share/url?url=${encodedUrl}`;
        break;
      case 'copy':
        navigator.clipboard.writeText(url);
        alert('🔗 Đã sao chép liên kết!');
        return;
      default:
        return;
    }

    window.open(shareUrl, '_blank', 'noopener,noreferrer');
  };

  return (
    <Modal show={show} onHide={onHide} centered className="share-modal-glass">
      <Modal.Header closeButton className="border-0">
        <Modal.Title className="text-white w-100 text-center">📤 Chia sẻ trang</Modal.Title>
      </Modal.Header>
      <Modal.Body className="d-flex flex-column align-items-center gap-3">
        <Button className="share-btn-lg facebook" onClick={() => handleShare('facebook')}>
          <i className="bi bi-facebook me-2"></i> Facebook
        </Button>
        <Button className="share-btn-lg twitter" onClick={() => handleShare('twitter')}>
          <i className="bi bi-twitter-x me-2"></i> Twitter
        </Button>
        <Button className="share-btn-lg telegram" onClick={() => handleShare('telegram')}>
          <i className="bi bi-telegram me-2"></i> Telegram
        </Button>
        <Button className="share-btn-lg zalo" onClick={() => handleShare('zalo')}>
          <img src="/assets/shared/img/zalo.svg" alt="Zalo" width={24} className="me-2" /> Zalo
        </Button>
        <Button className="share-btn-lg copy" onClick={() => handleShare('copy')}>
          <i className="bi bi-link-45deg me-2"></i> Sao chép liên kết
        </Button>
      </Modal.Body>
    </Modal>
  );
};

export default ShareModal;
